import React, { useState } from 'react';
import { Box } from '@mui/material';
import { DataGrid } from '@mui/x-data-grid';
import ItemDrawer from './ItemDrawer';
import { useDashboard } from '../context/DashboardContext';

export default function ItemsTable({ items, total, page, pageSize, loading, onPageChange, onPageSizeChange, onSortChange }) {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [selected, setSelected] = useState(null);
  const { setFilters } = useDashboard();

  const rows = items.map(it => ({ id: it.id, title: it.title, category: it.category, status: it.status, sales: it.sales, date: it.date }));
  const columns = [
    { field: 'title', headerName: 'Title', flex: 1 },
    { field: 'category', headerName: 'Category', width: 140 },
    { field: 'status', headerName: 'Status', width: 120 },
    { field: 'sales', headerName: 'Sales', width: 120, type: 'number' },
    { field: 'date', headerName: 'Date', width: 180 }
  ];

  return (
    <Box sx={{ height: 600, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        pageSize={pageSize}
        rowsPerPageOptions={[5,10,25,50]}
        rowCount={total}
        pagination
        paginationMode="server"
        onPageChange={(newPage) => onPageChange(newPage + 1)}
        onPageSizeChange={(newSize) => onPageSizeChange(newSize)}
        sortingMode="server"
        onSortModelChange={(model) => {
          if (model[0]) onSortChange(model[0].field === 'sales' ? 'sales' : 'date', model[0].sort);
        }}
        loading={loading}
        onRowClick={(params) => { setSelected(params.row); setDrawerOpen(true); }}
      />
      <ItemDrawer open={drawerOpen} itemId={selected?.id} onClose={() => setDrawerOpen(false)} />
    </Box>
  );
}
